* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'open sans', sans-serif, Arial, 'Century Gothic';
  background: -webkit-gradient(linear, left top, left bottom, from(#2f4050), to(#5c7d89));
  background: -webkit-linear-gradient(#2f4050 0%, #5c7d89 100%);
  background: -o-linear-gradient(#2f4050 0%, #5c7d89 100%);
  background: linear-gradient(#2f4050 0%, #5c7d89 100%);
  overflow-x: hidden;
  height: 100vh;
  max-height: 100vh;
  width: 100vw;
}

-webkit-scrollbar {
  display: none;
}

#root {
  height: 100vh;
  max-height: 100vh;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: 'open sans', sans-serif, Arial, 'Century Gothic';
  margin-bottom: 0px;
}

label {
  margin: 0;
}

